<template>
  <v-snackbar v-model="alert.show" :color="alert.type" :timeout="alert.timeout">
    <div>{{ $t(alert.text) }}</div>
    <template v-slot:action="{ attrs }">
      <v-btn icon v-bind="attrs" text @click="alert.show = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
export default {
  props: {
    alert: {
      type: Object,
      default: () => {
        return {}
      }
    }
  }
}
</script>

