import objectToFormData from '~/utils/createFormData'

export default {
  data() {
    return {
      loading: {
        data: false,
        add: false,
        edit: false,
        delete: false
      },
      alert: {
        show: false,
        type: 'success',
        text: '',
        timeout: 5000
      }
    }
  },
  methods: {
    // crud actions

    // fetch item action
    async fetchItem() {
      // replace the url param with actual param
      const fetchUrl = this.fetchData.url
      const url = fetchUrl.replace(':param', this.fetchData.param)

      try {
        // set loading to true
        this.loading.data = true

        // send request to api
        const response = await this.$axios.get(url)

        // set loading to false
        this.loading.data = false

        // set item
        this.item = response.data[this.fetchData.data]
      } catch (e) {
        // set loading to false
        this.loading.data = false
      }
    },

    // fetch items action
    async fetchItems(firstEmpty = false) {
      try {
        // set items to empty begore fetch
        if (firstEmpty) {
          this.data = []
        }

        // set loading to true
        this.loading.data = true

        // send request to api
        const response = await this.$axios.get(this.fetchData.url)

        // set loading to false
        this.loading.data = false

        // set data to respose
        this.data = response.data[this.fetchData.data]
      } catch (e) {
        // set loading to false
        this.loading.data = false
      }
    },

    // add item action
    async addItem(data, form, itemForm) {
      try {
        // remove any alerts if exist
        this.removeAlert()

        // set loading to true
        this.loading.add = true

        // convert data to formdata
        if (this.addData.formData) {
          data = objectToFormData(data)
        }

        // send api request
        const response = await this.$axios.post(this.addData.url, data)

        // set loading to false
        this.loading.add = false

        // reset form validation
        form.resetValidation()

        // reset form
        // this.item = Object.assign({}, this.defaultItem)

        // close dialog
        this.showAddEditDialog = false

        // display success message
        this.addAlert(
          true,
          'success',
          response.data.message ? response.data.message : response.message
        )

        // emit after add function
        this.$emit('after-add', response, this.item)

        // reload
        if (this.addData.reload) {
          if (response) {
            await this.fetchItems()
          }
        }

        // success redirect
        if (this.addData.successRedirect) {
          this.$router.push(this.addData.successRedirect)
        }
      } catch (e) {
        // set loading to false
        this.loading.add = false

        // display error message
        this.addAlert(
          true,
          'error',
          e.response.data.message ? e.response.data.message : e.response.message
        )
      }

      // error redirect
      if (this.addData.errorRedirect) {
        this.$router.push(this.addData.errorRedirect)
      }
    },

    // edit item aciotn
    async editItem(data, form) {
      // replace the url param with actual param
      const editUrl = this.editData.url
      let url = editUrl
      if (this.editData.param) {
        url = editUrl.replace(':param', this.item[this.editData.param])
      }

      try {
        // remove any alerts if exist
        this.removeAlert()

        // set loading to true
        this.loading.edit = true

        // convert data to formdata
        if (this.editData.formData) {
          data = objectToFormData(data)
        }

        // send request to api
        const response = await this.$axios.put(url, data)

        // set loading to false
        this.loading.edit = false

        // reset form validation
        form.resetValidation()

        // reset form
        // this.item = Object.assign({}, this.defaultItem)

        // close dialog
        this.showAddEditDialog = false

        // display success message
        this.addAlert(
          true,
          'success',
          response.data.message ? response.data.message : response.message
        )
        // reload
        if (this.editData.reload) {
          if (response) {
            await this.fetchItems()
          }
        }

        // emit after edit function
        this.$emit('after-edit', response, this.item)

        // success redirect
        if (this.editData.successRedirect) {
          this.$router.push(this.editData.successRedirect)
        }
      } catch (e) {
        // set loading to false
        this.loading.edit = false

        // display error message
        this.addAlert(
          true,
          'error',
          e.response.data.message ? e.response.data.message : e.response.message
        )

        // error redirect
        if (this.editData.errorRedirect) {
          this.$router.push(this.editData.errorRedirect)
        }
      }
    },

    // delete Item
    async deleteItem() {
      // replace the url param with actual param
      const deleteUrl = this.deleteData.url
      const url = deleteUrl.replace(
        ':param',
        this.itemToDel[this.deleteData.param]
      )
      try {
        // remove any alerts if exist
        this.removeAlert()

        // set loadign to true
        this.loading.delete = true

        // send request to api
        const response = await this.$axios.delete(url)

        // set loading to false
        this.loading.delete = false

        // display success message
        this.addAlert(
          true,
          'success',
          response.data.message ? response.data.message : response.message
        )

        // close confirm dialog
        this.showDeleteDialog = false

        // emit after delete function
        this.$emit('after-delete', response, this.item)

        // reload
        if (this.deleteData.reload) {
          if (response) {
            await this.fetchItems()
          }
        }
      } catch (e) {
        // set loading to false
        this.loading.delete = false

        // display error message
        this.addAlert(
          true,
          'error',
          e.response.data.message ? e.response.data.message : e.response.message
        )
      }
    },

    // delete multiple items
    async deleteItems(data) {
      try {
        // remove any alerts if exist
        this.removeAlert()

        // set loading to true
        this.loading.delete = true

        // if arrayOf given
        if (this.deleteMultipleData.arrayOf) {
          data = data.map((d) => d[this.deleteMultipleData.arrayOf])
        }

        // send request to api
        const response = await this.$axios.post(
          this.deleteMultipleData.url,
          data
        )
        // set loading to false
        this.loading.delete = false
        // display success message
        this.addAlert(
          true,
          'success',
          response.data.message ? response.data.message : response.message
        )
        // close confirm dialog
        this.showDeleteDialog = false

        // emit after delete multiple function
        this.$emit('after-delete-multiple', response, this.item)

        // reload
        if (this.deleteMultipleData.reload) {
          if (response) {
            await this.fetchItems()
          }
        }

        // change bulk delete value to default
        this.bulkDeleteValue = ''
      } catch (e) {
        // set laodign to false
        this.loading.delete = false
        // display error message
        this.addAlert(
          true,
          'error',
          e.response.data.message ? e.response.data.message : e.response.message
        )
      }
    },

    // add alert
    addAlert(show = true, type = 'info', text = '', timeout = 5000) {
      this.alert = {
        show,
        type,
        text,
        timeout
      }
    },

    // remove alert
    removeAlert(show = false, type = 'info', text = '', timeout = 5000) {
      this.alert = {
        show,
        type,
        text,
        timeout
      }
    }
  }
}
