<template>
  <div>
    <v-skeleton-loader
      v-if="loading.data"
      type="card-avatar, article, card-actions, card-avatar, article, card-actions"
      loading
    >
    </v-skeleton-loader>
    <v-card v-else>
      <v-card-title v-if="heading">
        {{ heading }}
        <v-spacer></v-spacer>
      </v-card-title>
      <v-card-text>
        <v-form
          id="itemForm"
          ref="form"
          v-model="valid"
          enctype="multipart/form-data"
        >
          <slot name="crud-form" :item="item"></slot>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          v-if="goBackButton"
          :color="goBackButton.color ? goBackButton.color : 'error'"
          @click="goBack"
        >
          {{
            goBackButton.text
              ? goBackButton.text
              : $t('crudEdit.goBackButton.text')
          }}
        </v-btn>
        <v-btn
          v-if="button"
          :loading="loading.edit"
          :color="button.color ? button.color : 'success'"
          @click="saveForm"
        >
          {{ button.text ? button.text : $t('crudEdit.editButton.text') }}
        </v-btn>
      </v-card-actions>
    </v-card>

    <!-- Alert Snacbar -->
    <crud-snackbar :alert="alert" />
  </div>
</template>

<script>
import crud from '@/mixins/crud'
import CrudSnackbar from '@/components/crud/CrudSnackbar'
export default {
  components: { CrudSnackbar },
  mixins: [crud],
  props: {
    heading: { type: String, default: '' },
    defaultItem: {
      type: Object,
      default: () => {
        return {}
      }
    },
    button: { type: [Boolean, Object], default: false },
    goBackButton: { type: [Boolean, Object], default: false },
    fetchData: { type: [Boolean, Object], default: false },
    editData: { type: [Boolean, Object], default: false }
  },
  data() {
    return {
      valid: false,
      item: Object.assign({}, this.defaultItem)
    }
  },
  // fetch items if fetch data
  async created() {
    if (this.fetchData) {
      await this.fetchItem()
    }
  },
  methods: {
    // save form
    saveForm() {
      this.$refs.form.validate()
      if (this.valid) {
        this.itemEdit(this.$refs.form)
      }
    },

    goBack() {
      if (this.goBackButton.redirect) {
        this.$router.push({ path: this.goBackButton.url })
      } else {
        this.$router.go(-1)
      }
    },

    // item edit
    async itemEdit(form) {
      if (!this.editData) {
        this.$emit('editItem', this.item, form)
      } else {
        await this.editItem(this.item, form, false)
      }
    }
  }
}
</script>

<style scope></style>
