<template>
  <v-card-text>
    <div class="d-flex flex-wrap align-center justify-space-between">
      <div v-for="(value, name) in colors" :key="name" class="mb-5">
        <h3 class="mb-2">{{ name.replace(/_/g, ' ') }}</h3>
        <v-select
          v-if="getThemeColors(name).length > 0"
          v-model="colors[name]"
          :items="getThemeColors(name)"
          label="Theme Colors"
          item-text="text"
          item-value="value"
          @change="getValue($event, name)"
        ></v-select>
        <div v-if="!disable">
          <v-color-picker
            v-if="!disabled.includes(name)"
            v-model="colors[name]"
            :disabled="disabled.includes(name)"
            mode="hexa"
          ></v-color-picker>
          <v-color-picker
            v-else
            :disabled="disabled.includes(name)"
          ></v-color-picker>
        </div>
      </div>
    </div>
  </v-card-text>
</template>

<script>
export default {
  props: {
    colors: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      themeColorsAllow: [
        'app_navbar_color',
        'app_navbar_text_color',
        'app_navbar_active_text_color',
        'app_navbar_scrolling_color',
        'app_navbar_scrolling_text_color',
        'app_navbar_scrolling_active_text_color',
        'app_navigation_color',
        'app_navigation_text_color',
        'app_navigation_active_text_color',
        'app_toolbar_color',
        'dashboard_navbar_color',
        'dashboard_navbar_text_color',
        'dashboard_navbar_active_text_color',
        'dashboard_navigation_color',
        'dashboard_navigation_text_color',
        'dashboard_navigation_active_text_color',
        'dashboard_toolbar_color'
      ],
      transparentColorAllow: [
        'app_navbar_color',
        'app_navbar_scrolling_color',
        'app_navigation_color',
        'app_toolbar_color',
        'dashboard_navbar_color',
        'dashboard_navigation_color',
        'dashboard_toolbar_color'
      ],
      themeColors: [
        { text: 'none', value: null },
        { text: 'transparent', value: 'transparent' },
        { text: 'primary', value: 'primary' },
        { text: 'secondary', value: 'secondary' },
        { text: 'tirtiary', value: 'tirtiary' },
        { text: 'success', value: 'success' },
        { text: 'error', value: 'error' },
        { text: 'info', value: 'info' },
        { text: 'warning', value: 'warning' },
        { text: 'accent', value: 'accent' }
      ],
      disabled: [],
      prevValues: {},
      items: this.colors,
      disable: true
    }
  },
  computed: {},
  mounted() {
    this.disable = true
    // eslint-disable-next-line
    for (const value in this.colors) {
      if (!this.colors[value].includes('#')) {
        this.disabled.push(value)
      }
    }
    this.disable = false
  },
  methods: {
    getThemeColors(name) {
      let arr
      if (this.themeColorsAllow.includes(name)) {
        arr = this.themeColors
      } else {
        arr = []
      }
      arr = arr.filter((color) => {
        if (color.text === 'transparent') {
          if (this.transparentColorAllow.includes(name)) {
            return true
          } else {
            return false
          }
        } else {
          return true
        }
      })
      return arr
    },
    getValue(value, item) {
      if (value) {
        const f = this.disabled.find((d) => d === item)
        if (!f) {
          this.disabled.push(item)
        }
      } else {
        this.colors[item] = '#ffffff'
        this.disabled = this.disabled.filter((d) => d !== item)
      }
    }
  }
}
</script>

